<template>
    <div>
        <adt-btns
            :titleNames="titleNames"
            :btnIndex="btnIndex"
            @handleBtn="goToOtherProductList"
            :width="160">
        </adt-btns>
        <adt-search-folder
            :searchRouter="{ query: search }"
            :isStatus="true"
            :isTitle="false">
            <template slot="status">
                <div class="status">
                    <Col span="22">
                        <div class="name">供应商产品状态：</div>
                        <span v-for="status in productStatus" :key="status.dictionaryKey" @click="auditStatus(status.dictionaryKey)" :class="{'click-status': search.serverStatus === status.dictionaryKey}">
                            {{ status.dictionaryValue }}
                        </span>
                    </Col>
                </div>
            </template>
            <template slot="always-show">
                <!-- 运输产品 -->
                <Col span="8" class="form-item">
                    <span style="width: 70px; flex-shrink: 0;">国家城市:</span>
                    <adt-city-selector @on-change="changeCity" :defaultData="search.city"></adt-city-selector>
                    <span style="width: 70px; margin-left: 12px;flex-shrink: 0;">选择场所:</span>
                    <Select v-model="search.site" clearable>
                        <Option v-for="(option, index) in siteOptions" :value="option.value" :key="index">{{option.label}}</Option>
                    </Select>
                </Col>
                <Col span="5" offset="1" class="form-item">
                    <span style="margin-right: 12px; text-align: right;">产品编号:</span>
                    <Input v-model="search.serverCode" clearable />
                </Col>
            </template>
        </adt-search-folder>
        <div class="btns-paginator">
            <adt-button :selectList="selectList">
                <Button @click="addProduct()">新建产品<Icon type="ios-add-circle-outline" /></Button>
                <!-- <Button @click="setDiscount()">设置优惠折扣<Icon type="ios-add-circle-outline" /></Button>
                <Button @click="cancelDiscount()">取消优惠折扣<Icon type="ios-add-circle-outline" /></Button> -->
                <Button @click="validApply()">{{ '有效' + ( isManager ? '' : '申请') }}<Icon type="ios-add-circle-outline" /></Button>
                <Button @click="invalidApply()">{{ '失效' + ( isManager ? '' : '申请') }}<Icon type="ios-add-circle-outline" /></Button>
            </adt-button>
            <adt-paginator :currentPage="~~page" :total="~~total" />
        </div>
        <div class="public-component-line"></div>
        <div class="public-list three-column">
            <div class="public-component-card" v-for="(product, index) in listData" :key="index">
                <adt-list-item :logistics="product" :showCheckbox="true" :resetCheckStatus="resetCheckStatus" @checkData="checkData">
                    <template slot="tab-title">
                        <span>产品编号:{{ product.serverCode }}</span>
                    </template>
                    <template slot="tab-operat">
                        <span :class="{'card-status-success': product.serverStatus === 'upload' || product.serverStatus === '上载' || product.serverStatus === 'enable' || product.serverStatus === '有效' || product.serverStatus === 'wait' || product.serverStatus === '待审核', 'card-status-error': product.serverStatus === 'fail' || product.serverStatus === '审核未通过', 'card-status-disable': product.serverStatus === 'disable' || product.serverStatus === '失效' }">{{ product.serverStatus }}</span>
                        <Poptip placement="right-start" trigger="hover" class="card-icon">
                            <span>详情</span>
                            <Icon type="md-arrow-dropdown" />
                            <div class="card-poptip-font" slot="content">
                                <!-- <div @click="examine(product)">审核</div> -->
                                <div @click="examine(product)" v-if="isManager && (product.serverStatus === 'wait' || product.serverStatus === '待审核')">审核</div>
                                <div @click="validApply(product)" v-if="product.serverStatus === 'upload' || product.serverStatus === '上载' || product.serverStatus === 'disable' || product.serverStatus === '失效' || product.serverStatus === 'fail' || product.serverStatus === '审核未通过'">{{ '有效' + ( isManager ? '' : '申请') }}</div>
                                <div @click="invalidApply(product)" v-if="product.serverStatus === 'enable' || product.serverStatus === '有效' || product.serverStatus === 'fail' || product.serverStatus === '审核未通过'">{{ '失效' + ( isManager ? '' : '申请') }}</div>
                                <div @click="detail(product)">详情</div>
                                <!-- <div @click="edit(product)">编辑</div> -->
                                <!-- <div @click="remove(product)" class="failure">删除</div> -->
                            </div>
                        </Poptip>
                    </template>
                    <template slot="tab-content">
                        <!-- 场所产品 -->
                        <div class="tab-content">
                            <div class="province">{{ product.sttnCode.split(';').slice(0, product.sttnCode.split(';').length - 1).join('') }}</div>
                            <div class="site">{{ product.sttnCode.split(';').pop() }}</div>
                            <template v-if="product.PrefDiscount">
                                <div class="account"><span>{{ product.PrefDiscount }}</span>%</div>
                                <svg class="iconfont account-tag" aria-hidden="true">
                                    <use xlink:href="#icon-andaotongcaidanICON_huaban28youhuizhekou"></use>
                                </svg>
                            </template>
                        </div>
                    </template>
                    <template slot="tab-bottom">
                        <div class="tab-bottom">
                            <Row>
                                <Col span="24">有效期：{{ timestampFormat(product.serverStartTime) }} -- {{ timestampFormat(product.serverEndTime) }}</Col>
                            </Row>
                            <Row>
                                <Col span="10">执行时长：{{ product.totalTime }}{{ product.totalTimeUnit }}</Col>
                                <Col span="14" class="text-right">价格：￥<span>{{ product.priceTotal }}</span>/{{ (product.trspType === 'C' || product.trspType === '集装箱') ? 'TEU' : 'KG' }}</Col>
                                <!-- <Col span="12">发布价格：￥<span>{{ product.priceTotal }}</span>/{{ product.cargoType }}</Col> -->
                            </Row>
                        </div>
                    </template>
                </adt-list-item>
            </div>
        </div>
        <!-- 设置优惠折扣比modal -->
        <adt-my-modal
            title="设置优惠折扣比"
            :showModal="isModalShow"
            @on-visible-change="visibleChange"
            @on-ok="confirmSetDiscount()">
            <Form
                ref="formValidate"
                inline
                :model="discountData"
                :rules="ruleValidate"
                :label-colon="true"
                label-position="left"
                :label-width="120">
                <FormItem label="优惠折扣比" prop="discount">
                    <Input v-model="discountData.discount" placeholder="输入1~99之间的数字">
                        <span slot="suffix" style="line-height: 32px;">%</span>
                    </Input>
                </FormItem>
            </Form>
        </adt-my-modal>
        <!-- 取消优惠折扣 -->
        <adt-reconfirm-modal
            :showModal="isCancelDiscountModalShow"
            message="确认取消勾选产品的优惠折扣吗？"
            title="取消折扣"
            @on-visible-change="cancelDiscountVisibleChange"
            @on-ok="confirmCancelDiscount()"
        />
        <!-- 审核modal -->
        <adt-my-modal
            :title="examineModalTitle"
            :showModal="isExamineModalShow"
            @on-visible-change="examineVisibleChange"
            @on-ok="examineSave()"
            width="600">
            <RadioGroup v-model="agree" style="margin-bottom: 20px;">
                <Radio label="1">同意{{ examineType === 1 ? '有效' : '失效' }}</Radio>
                <Radio label="0" style="margin-left: 30px;">不同意{{ examineType === 1 ? '有效' : '失效' }}</Radio>
            </RadioGroup>
            <div v-show="agree === '0'">
                <div class="reason-name" style="margin-bottom: 6px;">不同意原因备注:</div>
                <Input type="textarea" v-model.trim="reason" placeholder="请输入内容..." />
            </div>
        </adt-my-modal>
        <!-- 失效审核：产品已经与方案绑定 -->
        <adt-reconfirm-modal title="失效审核" :message="sxMessage" :showModal="isSxModalShow" @on-visible-change="sxVisibleChange" @on-ok="confirmSx()" />
        <!-- 删除 -->
        <adt-reconfirm-modal :showModal="isDeleteModalShow" @on-visible-change="deleteVisibleChange" @on-ok="confirmDelete()" />
    </div>
</template>

<script>
    import productList from '@/mixins/productList'
    import {
        apiStationQueryList,
        apiStationAuditTrue,
        apiStationAuditFalse,
        apiStationAuditTrueOk,
        apiStationAuditTrueNo,
        apiStationAuditFalseOk,
        apiStationAuditFalseNo,
        apiStationPrefPrice,
        apiStationOffPrice,
    } from '@/api/productEditor'
    export default {
        mixins: [productList],
        props: {
            city: '',
            site: '',
            serverStatus: '',
            serverCode: ''
        },
        data () {
            return {
                search: {
                    city: this.city,
                    site: this.site,
                    serverStatus: this.serverStatus,
                    serverCode: this.serverCode
                },
                siteOptions: [],
                btnIndex: 2
            }
        },
        async created () {
            // 根据默认的城市获取场所
            if (this.search.city) {
                this.siteOptions = await this._handelDir(this.search.city.split('~'))
            }
        },
        methods: {
            getData () {
                let city = this.search.city ? this.search.city.split('~') : []
                const data = {
                    page: this.page,
                    sttnCode: city.length ? `${city.join(';')};${this.search.site || ''}` : '',
                    serverStatus: this.search.serverStatus,
                    serverCode: this.search.serverCode
                }
                // 去除选中的状态（写两遍是为了触发watch监听）
                this.resetCheckStatus = false
                apiStationQueryList({ data }).then(({ data }) => {
                    this.listData = data.content || []
                    this.total = data.totalElements
                    // 重置选中的值
                    this.selectList = []
                    // 去除选中的状态
                    this.resetCheckStatus = true
                })
            },
            validApply (dataSource) {
                const data = this.handleRequestData(dataSource)
                // 批量操作，没有选择产品
                if (!data.length) return
                apiStationAuditTrue({ data }).then(res => {
                    this.$Message.info(res.message)
                    this.getData()
                })
            },
            invalidApply (dataSource) {
                const data = this.handleRequestData(dataSource)
                // 批量操作，没有选择产品
                if (!data.length) return
                apiStationAuditFalse({ data}).then(res => {
                    this.$Message.info(res.message)
                    this.getData()
                })
            },
            examineSave () {
                const vm = this
                const data = { ...this.examineInfo}
                // 有效审核
                if (this.examineType === 1) {
                    // 同意
                    if (this.agree === '1') {
                        apiStationAuditTrueOk(data).then(res => {
                            judgeIsBind(res)
                        })
                    } else {
                        if (!this.reason) {
                            this.$Message.warning('请填写不同意原因！')
                            return
                        }
                        data.remark = this.reason
                        // 不同意
                        apiStationAuditTrueNo({ data }).then(res => {
                            judgeIsBind(res)
                        })
                    }
                } else {
                    // 失效审核
                    // 同意
                    if (this.agree === '1') {
                        apiStationAuditFalseOk(data).then(res => {
                            judgeIsBind(res)
                        })
                    } else {
                        if (!this.reason) {
                            this.$Message.warning('请填写输入驳回原因！')
                            return
                        }
                        data.remark = this.reason
                        // 不同意
                        apiStationAuditFalseNo({ data }).then(res => {
                            judgeIsBind(res)
                        })
                    }
                }
                function judgeIsBind (res, isAuditFalse) {
                    // res.data: {true: 没绑定, false: 已绑定方案}
                    // examineType === 0: 失效审核
                    // 产品已经与方案绑定
                    if (!res.data && this.examineType === 0) {
                        vm.isSxModalShow = true
                        vm.sxMessage = `产品编号：${data.serverCode} 产品已经与方案绑定，失效申请需要调度审核，确定提交调动审核？`
                    } else {
                        vm.$Message.info(res.message)
                        vm.isExamineModalShow = false
                        vm.getData()
                    }
                }
            },
            confirmSx () {
                api(this.examineInfo).then(res => {
                    this.$Message.info(res.message)
                    this.isExamineModalShow = false
                    this.isSxModalShow = false
                })
            },
            confirmSetDiscount () {
                this.$refs.formValidate.validate((valid) => {
                    if (valid) {
                        const discount = Number(this.discountData.discount)
                        if (!discount) {
                            this.$Message.warning('请输入数字！')
                            return
                        }
                        if (discount < 1 || discount > 99) {
                            this.$Message.warning('优惠折扣比要在1~99之间！')
                            return
                        }
                        apiStationPrefPrice({ data: { serverCodes: this.requestData, prefDiscount: discount }}).then(res => {
                            this.$Message.info(res.message)
                            this.getData()
                        })
                    }
                })
            },
            confirmCancelDiscount () {
                api().then(res => {
                    this.$Message.info(res.message)
                    this.getData()
                })
            },
            addProduct () {
                this.$router.push('/product-editor/add-site-product')
            },
            edit (data) {
                this.$router.push('')
            },
            detail (data) {
                this.$router.push(`/product-editor/product-detail/${data.serverCode}?type=site`)
            },
            remove (data) {
                this.deleteId = id
            },
            confirmDelete () {

            },
            // 选择国家城市
            async changeCity (value) {
                this.search.city = value.value.join('~')
                this.siteOptions = await this._handelDir(value.value)
            }
        },
    }
</script>

<style lang="scss" scoped>
    @import './style.scss';
</style>
